@import url('https://fonts.googleapis.com/css?family=Roboto');

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
body {
  color: #576366;
  background: #5fcf80;
  margin: 0;
  font-family: Roboto,sans-serif;
}
.container {
  width: 95%;
  max-width: 780px;
  padding-bottom: 2em;
  margin: 0 auto 2em;
  background: #fff;
  border-radius: .5em;
  -webkit-box-shadow: 0 3px rgba(0,0,0,.1);
  box-shadow: 0 3px rgba(0,0,0,.1);
  overflow: hidden;
}
header {
  background: #edeff0;
  margin-bottom: 2em;
}
.icn-logo {
  background: #54606b;
  color: #fff;
}
.icn-logo i {
  font-size: 2.45em;
  line-height: 0;
  margin-top: 30px;
}
ul {
  list-style-type: none;
}
h1, h2, ul {
  margin: 0;
  padding: 0;
}
li {
  display: list-item;
  text-align: -webkit-match-parent;
}
a {
  text-decoration: none;
  color: #3b80d1;
}
.main-nav a, .not-found {
  text-align: center;
}
.main-nav a {
  font-size: .95em;
  color: #222;
  text-transform: uppercase;
  display: block;
  padding: 1em;
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
}
.main-nav .active, .main-nav a:hover {
  color: #fff;
  background: #3b80d1;
}
.main-content {
  padding: 1em 2em;
}
h2 {
  color: #404040;
  font-size: 1.6em;
  margin-bottom: 1.25em;
}
.teacher-img {
  width: 100%;
  border-radius: .3em;
}
.group:after {
  content: "";
  display: table;
  clear: both;
}
.course-nav a.active, .course-nav a:hover {
  color: #fff;
  background: #39add1;
}
.course-nav li{
  margin-left: .6em;
}
.course-nav a {
  font-size: .85em;
  color: #7d7d7d;
  padding: .2em .8em;
  border-radius: .25em;
  background: #eaebeb;
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
}
.course {
  margin-bottom: 1em;
}
.course-img {
  width: 70px;
}

@media (min-width: 769px){
  body {
    padding-top: 3em;
  }
  .icn-logo {
    margin-right: auto;
    padding: 0 2em;
  }
  .main-nav, header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
  }
  .main-nav a {
    border-left: 3px solid transparent;
    border-right: 3px solid transparent;
    padding: 1.25em 1.5em;
  }
  .teacher {
    width: 48.73%;
    margin-bottom: 1.5em;
  }
  .media>div, .media img, .teacher {
    float: left;
  }
  .courses h2 {
    float: left;
  }
  .course-nav {
    float: right;
  }
  .course-nav li {
    display: inline-block;
  }
  .media>div {
    width: 85.1%;
    margin-left: 1.5em;
  }
}


